<template>
  <div class="playlist">
   <div class="playlist-top">
     <p>我是顶部导航</p>
     <p>我是顶部导航</p>
     <p>我是顶部导航</p>
     <p>我是顶部导航</p>
     <p>我是顶部导航</p>
     <p>我是顶部导航</p>
     <p>我是顶部导航</p>
     <p>我是顶部导航</p>
   </div>
    <div class="playdateil">
      <div class="top">
        <h1>全部歌单</h1>
        <div class="top-info">
          <span
            class="top-info-item"
            :class="parma.order === 'hot' ? 'active' : ''"
            @click="selectOrder('hot')"
            >热门</span
          >
          <span
            class="top-info-item"
            :class="parma.order === 'new' ? 'active' : ''"
             @click="selectOrder('new')"
            >最新</span
          >
        </div>
      </div>
      <div class="bottom">
        <hot-list-item
          v-for="(item, index) in allPlayList"
          :key="index"
          :hotList="item"
        ></hot-list-item>
        <h1 v-if="allPlayList.length == 0" class="dataNo">暂无数据</h1>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, reactive, toRefs } from "@vue/runtime-core";
import { useStore } from "vuex";
import hotListItem from "../../components/hotListItem.vue";

export default {
  name: "PlayList",
  components: {
    hotListItem,
  },
  setup() {
    const store = useStore();
    const listInfo = reactive({
      parma: {
        order: "hot",
        cat: "",
        limit: 18,
        offset: 0,
      },
    });

    const selectOrder  = (type) => {
      listInfo.parma.order = type
      console.log(listInfo.parma.order);
      init()
    }

    const allPlayList = computed(() => {
      return store.state.home.playListHot;
    });

    const init = () => {
      store.dispatch("getPlayListHot", listInfo.parma);
    };

    onMounted(() => {
      init();
    });

    return {
      allPlayList,
      ...toRefs(listInfo),
      selectOrder
    };
  },
};
</script>

<style lang="scss" scoped>
.playlist {
  margin-top: 20px;
  .playlist-top{
        border-radius: 10px;
    background: linear-gradient(145deg, #f6f6f6, #cfcfcf);
    box-shadow: 6px 6px 13px #bdbdbd, -6px -6px 13px #ffffff;
    margin-bottom: 30px;
  }
  .top {
    display: flex;
    text-align: left;
    align-items: center;
    h1 {
      font-size: 25px;
      margin-right: 50px;
    }
    .top-info-item {
      display: inline-block;
      padding: 0 12px;
      font-size: 14px;
      margin-bottom: 5px;
      border-radius: 5px;
    }
  }
  .bottom {
    display: flex;
    flex-wrap: wrap;
    .dataNo{
     margin: 0 auto;
    }
  }
}
.active {
  background: linear-gradient(145deg, #e69200, #ffad00);
}
</style>
